.head_event {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  min-width: 1260px;
  height: 100px; }
  .head_event .tit_logo {
    position: absolute;
    top: 23px;
    left: 50px; }
    .head_event .tit_logo .link_logo {
      display: block; }
  .head_event .list_util {
    overflow: hidden;
    position: absolute;
    top: 28px;
    right: 70px; }
    .head_event .list_util li {
      float: left; }
      .head_event .list_util li + li {
        margin-left: 28px; }
    .head_event .list_util .link_home, .head_event .list_util .link_today {
      width: 15px;
      height: 13px;
      background-position: 0 -340px; }
    .head_event .list_util .link_today {
      width: 117px;
      background-position-x: -20px; }
